@import url(https://fonts.googleapis.com/css2?family=Rufina:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,200;0,300;0,400;0,600;0,700;0,800;0,900;1,200;1,300;1,400;1,600;1,700;1,800;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,200;0,300;0,400;0,600;0,700;0,800;0,900;1,200;1,300;1,400;1,600;1,700;1,800;1,900&display=swap);
.App {
  background-color: #fff2f2;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  padding: 0;
  margin: 0;
  /* background-color: #fff2f2; */
}

html {
  scroll-behavior: smooth;
}

h1 {
  color: #031532;
  font-family: "Rufina", serif;
}

h2 {
  color: #031532;
  font-size: 34px;
  font-family: "Rufina", serif;
}

.subh1 {
  font-family: "Rufina", serif;
  font-weight: 400;
  color: red;
  font-size: 22px;
}

p {
  color: #566573;
}
.footer {
    background-color: #fff2f2;
    color: #7a7a7a;
    padding: 20px;
}

.footer__content {
    display: flex;
    justify-content: center;
    align-items: center;
}
.footer__copyright {
    font-size: 14px;
}
.header {
  background-color: #fff2f2;
  color: black;
  font-size: 18px;
  font-weight: 700;
  font-family: "Nunito", san-serif;
}

.header__title_link {
  font-weight: bold;
  text-transform: uppercase;
  color: black;
}

.header__title_link:hover {
  color: black;
}

/*
.header__nav {
  display: flex;
  flex: 1;
  justify-content: center;
}
*/

.header__navOption {
  border: none;
  background-color: transparent;
  color: black;
  font-weight: bold;
  outline: none;
  font-size: 14px;
}

.header__navOption:hover {
  color: red;
  transition: 0.3s;
  cursor: pointer;
}

.about {
  /* width: 100%;
  height: 50vh; */
  background-color: #fff2f2;
  color: #566573;
  font-family: "Nunito", sans-serif;
}

.about__image {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
}

.about__image > img {
  object-fit: cover;
  /* width: 275px;
  height: 275px; */
  border-radius: 50%;
  /* border: 1px solid #a1acb3; */
}

.about__quote {
  margin: 15px 0;
  display: flex;
}

.about__indent {
  background-color: #01d1c1;
  width: 5px;
  margin-right: 15px;
}

.about__details {
  margin-top: 10px;
  display: flex;
  justify-content: space-between;
}

.about__name,
.about__phone {
  font-family: "Rufina", serif;
  font-size: 16px;
  color: #031532;
  width: 48%;
  display: flex;
  align-items: center;
}

.about__icon {
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 8px;
  border-radius: 50%;
  margin-right: 10px;
}

.about__nameIcon,
.about__linkedIn {
  background-color: #ff6665;
}

.about__phoneIcon,
.about__github {
  background-color: #01d1c1;
}

.about__instagram {
  background-color: #566573;
}

.about__email {
  margin-top: 10px;
  font-family: "Rufina", serif;
  color: #031532;
  display: flex;
  align-items: center;
  width: 70%;
}

.about__emailIcon {
  color: white;
  background-color: #031532;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 8px;
  border-radius: 50%;
}

.banner {
  /* width: 100%; */
  /* height: 50vh; */
  background-color: #fff2f2;
  color: #031532;
  display: flex;
  font-size: 30px;
  justify-content: center;
  align-items: center;
  font-family: "Rufina", serif;
}

.banner__title > h4 > span {
  -webkit-text-decoration: underline solid #01d1c1;
          text-decoration: underline solid #01d1c1;
}

.banner__greetings {
  display: flex;
}

.banner__greetings > p {
  margin-right: 20px;
}

.banner__subtitle {
  /* margin-top: 10px; */
  font-family: "Nunito", san-serif;
  font-size: 18px;
  color: #566573;
}

.banner__content {
  margin: 0 40px;
}

.banner__buttons {
  /* margin-top: 20px; */
  /* width: 50%; */
  /* display: flex; */
  /* justify-content: space-between; */
}

.learnMoreButton {
  width: 150px;
  height: 35px;
  color: #031532;
  font-family: "Nunito", sans-serif;
  font-size: 16px;
  background-color: #01d1c1 !important;
  font-weight: bold;
}

.learnMoreButton:hover {
  cursor: pointer;
}

.fill {
  position: relative;
}

.content {
  width: 150px;
  height: 35px;
  position: absolute;
}

.resumeButton {
  width: 150px;
  height: 35px;
  color: white;
  font-family: "Nunito", sans-serif;
  font-size: 16px;
  background-color: #ff6665 !important;
  font-weight: bold;
}

.resumeButton:hover {
  cursor: pointer;
}

.fill {
  position: relative;
}

.content {
  width: 150px;
  height: 35px;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
}

.content > .icon {
  margin-right: 5px;
}

.home {
    background-color: #fff2f2;
}
